import i18n from "i18next";
import Backend from "i18next-http-backend";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

import lang_en from "./lang/en.json";
// import lang_cn from "./lang/cn.json";
// import lang_cz from "./languages/czech.json";
// import lang_fr from "./languages/french.json";
// import lang_de from "./languages/german.json";
// import lang_hi from "./languages/hindi.json";
// import lang_pt from "./languages/portuguese.json";
// import lang_es from "./languages/spanish.json";

const resources = {
  en: { translation: lang_en },
  // cn: { translation: lang_cn },
  //   cz: { translation: lang_cz },
  //   fr: { translation: lang_fr },
  //   de: { translation: lang_de },
  //   hi: { translation: lang_hi },
  //   pt: { translation: lang_pt },
  //   es: { translation: lang_es },
};

i18n
  .use(Backend)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources,
    fallbackLng: "en",
    //debug: true,
    interpolation: { escapeValue: false },
    // keySeparator: false,
    detection: {
      order: ["localStorage"],
      lookupLocalStorage: "UILang",
    },
  });

export default i18n;
