import axios from "axios";
import { endpoints } from "../services/endpoints.config";
import { authenticationService } from "../services";

axios.interceptors.request.use(function (config) {
  var apiUrl = process.env.REACT_APP_DEV_API_URL;
  const companyId = localStorage.getItem("coId");
  const companyMemberId = localStorage.getItem("coMbrId");
  const companyMemberToken = localStorage.getItem("coMbrTkn");

  const excludedEndpoints = [
    endpoints.login,
    endpoints.companyRegister,
    endpoints.register,
    endpoints.forgotPassword,
    endpoints.resetPassword,
    endpoints.userOnboard,
    endpoints.companyCatalog,
    endpoints.endTest,
    endpoints.validateRegistration,
  ];

  if (companyMemberToken !== null && companyMemberToken === "") {
    authenticationService.logout();
    window.location.reload();
  } else {
    config.url = `${apiUrl}/${config.url}`;

    var url = new URL(config.url);
    var pathName = url.pathname.replace(/^\/[\w\d]+\//, "");
    var index = excludedEndpoints.findIndex((e) => e === pathName);
    config.headers["x-functions-key"] = process.env.REACT_APP_API_FUNCTIONS_KEY;

    if (index < 0) {
      config.headers["coId"] = companyId;
      config.headers["coMbrId"] = companyMemberId;
      config.headers["coMbrTkn"] = companyMemberToken;
    }

    return config;
  }
});

axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    var apiUrl = process.env.REACT_APP_DEV_API_URL;
    let loginEndpoint = `${apiUrl}/${endpoints.login}`;
    let registerEndpoint = `${apiUrl}/${endpoints.register}`;
    let forgotPasswordEndpoint = `${apiUrl}/${endpoints.forgotPassword}`;
    let resetPasswordEndpoint = `${apiUrl}/${endpoints.resetPassword}`;
    let userOnboardEndpoint = `${apiUrl}/${endpoints.userOnboard}`;
    let companyCatalogEndpoint = `${apiUrl}/${endpoints.companyCatalog}`;

    const excludedEndpoints = [
      loginEndpoint,
      registerEndpoint,
      forgotPasswordEndpoint,
      resetPasswordEndpoint,
      userOnboardEndpoint,
      companyCatalogEndpoint,
    ];

    var index = excludedEndpoints.findIndex(
      (item) => item === error.response?.config?.url
    );

    if (index !== -1) {
      return Promise.reject(error);
    }

    //Check if token has session timeout (440)
    if (
      error &&
      error.response &&
      error.response.status &&
      440 === error.response.status
    ) {
      authenticationService.logout();
      window.location.reload();
    }

    return Promise.reject(error);
  }
);
