import React from "react";
import styles from "./InteractionQuestionMode.module.css";
import keyboard from "../../images/user-question/keyboard329.svg";
import voice_on from "../../images/user-question/voice control active@2x.png";
import voice_off from "../../images/user-answer/voice control icon@2x.png";

const InteractionMode = {
  VOICE: "VOICE",
  TYPING: "TYPING",
};

const InteractionQuestionMode = (props) => {
  const { mode, onToggle } = props;
  return (
    <div className={styles.wrapper}>
      <div
        className={`${styles.btn} ${
          mode === InteractionMode.TYPING ? styles.clicked : styles.unclicked
        }`}
        onClick={() => onToggle(InteractionMode.TYPING)}
      >
        <div className={styles.innerbtn}>
          <img src={keyboard} alt="type" />
        </div>
      </div>
      <div
        className={`${styles.btn} ${
          mode === InteractionMode.VOICE ? styles.clicked : styles.unclicked
        }`}
        onClick={() => onToggle(InteractionMode.VOICE)}
      >
        <div className={styles.innerbtn}>
          <img
            src={mode === InteractionMode.VOICE ? voice_on : voice_off}
            className={styles.voice}
            alt="speaker"
          />
        </div>
      </div>
    </div>
  );
};

export default InteractionQuestionMode;
