import {
  GET_QUESTION,
  GET_QUESTION_PROGRESS,
  GET_ANSWER,
  FETCH_QUESTION_HISTORY,
  RATE_ANSWER,
  GET_FAILED_QUESTION,
  FETCH_LOAD_MORE_QUESTION_HISTORY,
  RESET_QUESTION_HISTORY,
  SEARCH_QUESTION_HISTORY,
} from "../actions/questionActions";

const initialState = {
  list: [],
  pageSize: 0,
  pageCount: 0,
  pageIndex: 1,
  orderBy: "",
  orderDir: "",
  searchBy: "",
  searchVal: "",
};

export default function questions(state = initialState, action) {
  var list, newList, findQuesIndex;

  switch (action.type) {
    case FETCH_QUESTION_HISTORY:
      return {
        ...state,
        list: action.payload.questions,
        pageSize: action.payload.pageSize,
        pageCount: action.payload.pageCount,
        orderBy: action.payload.orderBy,
        orderDir: action.payload.orderDir,
        searchBy: action.payload.searchBy,
        searchVal: action.payload.searchVal,
      };
    case FETCH_LOAD_MORE_QUESTION_HISTORY:
      var newQuestions = action.payload.questions;
      newList = Object.values(state.list);
      for (var i = 0; i < newQuestions.length; i++) {
        newList.push(newQuestions[i]);
      }
      return {
        ...state,
        list: newList,
        pageSize: action.payload.pageSize,
        pageCount: action.payload.pageCount,
        pageIndex: action.payload.pageIndex,
      };
    case RATE_ANSWER:
      list = Object.values(state.list);
      findQuesIndex = list.findIndex(function (item) {
        return item.Id === action.payload.questionId;
      });
      if (findQuesIndex === -1) {
        return {
          ...state,
          list: list,
        };
      } else {
        return {
          ...state,
          list: {
            ...state.list,
            [findQuesIndex]: {
              ...state.list[findQuesIndex],
              ans: {
                ...state.list[findQuesIndex].ans,
                RankPercentage: action.payload.percentage,
              },
            },
          },
        };
      }
    case GET_QUESTION_PROGRESS:
      list = state.list;
      findQuesIndex = list.findIndex(function (item) {
        return item.ques.Id === action.payload.questionId;
      });
      list[findQuesIndex].ques.Progress = action.payload.progress;
      return {
        ...state,
        list: [...list],
      };
    case GET_QUESTION:
      var newItem = {
        ans: {
          Text: "",
        },
        ques: {
          AskedAt: action.payload.askedAt,
          Id: action.payload.questionId,
          StatusId: action.payload.statusId,
          Text: action.payload.questionText,
          Progress: action.payload.progress,
        },
      };
      let tempList = state.list;
      tempList.push(newItem);
      return {
        ...state,
        list: tempList,
      };
    case GET_ANSWER:
      list = state.list;
      findQuesIndex = list.findIndex(function (item) {
        return item.ques.Id === action.payload.questionId;
      });

      if (findQuesIndex === -1) {
        return [...state];
      } else {
        list[findQuesIndex].ans.Text = action.payload.answer;
        return {
          ...state,
          list: [...list],
        };
      }
    case GET_FAILED_QUESTION:
      list = Object.values(state.list);
      findQuesIndex = list.findIndex(function (item) {
        return item.ques.Id === action.payload.questionId;
      });

      if (findQuesIndex === -1) {
        return {
          ...state,
          list: list,
        };
      } else {
        return {
          ...state,
          list: {
            ...state.list,
            [findQuesIndex]: {
              ...state.list[findQuesIndex],
              StatusId: "failed",
            },
          },
        };
      }
    case RESET_QUESTION_HISTORY:
      return {
        ...state,
        list: [],
        pageSize: 0,
        pageCount: 0,
        pageIndex: 1,
        orderBy: "",
        orderDir: "",
        searchBy: "",
        searchVal: "",
      };
    case SEARCH_QUESTION_HISTORY:
      return {
        ...state,
        list: action.payload.questions,
        pageSize: action.payload.pageSize,
        pageCount: action.payload.pageCount,
        pageIndex: action.payload.pageIndex,
        searchVal: action.payload.searchVal,
      };
    default:
      return state;
  }
}
