import { BehaviorSubject } from "rxjs";
import axios from "axios";
import { endpoints } from "./endpoints.config.js";

const currentJwt = new BehaviorSubject(localStorage.getItem("coMbrTkn"));

export const authenticationService = {
  login,
  logout,
  companyRegister,
  validateRegistration,
  userOnboard,
  photoWriteAccess,
  forgotPassword,
  forgetPassword,
  resetPassword,
  currentJwt: currentJwt.asObservable(),
  get currentJwtValue() {
    return currentJwt.value;
  },
  set currentJwtValue(value) {
    console.log(value);
    currentJwt.next(value);
  },
};

async function login(companyId, email, password, rememberMe) {
  var result = {};

  await axios
    .get(endpoints.login, {
      headers: {
        coId: companyId,
        email: email,
        pwd: password,
        rememberMe: rememberMe,
      },
    })
    .then((res) => {
      debugger;
      if (
        res != null &&
        res.data != null &&
        res.data.company != null &&
        res.data.company.member != null
      ) {
        localStorage.setItem("coId", res.data.company.id);
        localStorage.setItem("coMbrId", res.data.company.member.id);
        localStorage.setItem("coMbrTkn", res.data.company.member.token);
        localStorage.setItem("siloId", res.data.company.member.silo.id);
        currentJwt.next(res.data.company);
        result = res;
        result.successful = true;
      }
    })
    .catch((err) => {
      result.successful = false;
      result.error = {
        header: err.response.data.hdr,
        message: err.response.data.msg,
      };
    });

  return result;
}

function companyRegister(user) {
  var result = {};

  var formData = new FormData();
  formData.append("name", user.company);
  formData.append("location", "westeurope");
  formData.append("dbSkuName", "Basic");
  formData.append("dbSkuTier", "Basic");
  formData.append("mbrEmail", user.email);
  formData.append("mbrPwd", user.password);
  formData.append("mbrFName", user.firstName);
  formData.append("mbrLName", user.lastName);
  formData.append("mbrJob", user.position);
  formData.append("size", user.companySize);
  formData.append("mbrNickName", user.nickName);
  formData.append("mbrPhoneticName", user.phoneticName);
  return new Promise((resolve, reject) => {
    axios
      .post(endpoints.companyRegister, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        localStorage.setItem("coId", res.data.company.id);
        localStorage.setItem("coMbrId", res.data.company.member.id);
        localStorage.setItem("coMbrTkn", res.data.company.member.token);
        localStorage.setItem("siloId", res.data.company.member.silo.id);
        currentJwt.next(res.data.company);
        resolve(res);
      })
      .catch((err) => {
        console.log(err, "sign up error");
        result = {
          header: err.response ? err.response.data.hdr : "",
          message: err.response ? err.response.data.msg : "",
        };
        reject(result);
      });
  });
}

async function validateRegistration(coId, email) {
  return await axios.get(endpoints.validateRegistration, {
    headers: {
      coId,
      email,
    },
  });
}

function userOnboard(user) {
  var result = {};

  var formData = new FormData();
  user.password && formData.append("pwd", user.password);
  user.firstName && formData.append("fName", user.firstName);
  user.lastName && formData.append("lName", user.lastName);
  user.position && formData.append("job", user.position);
  user.phoneticName && formData.append("phoneticName", user.phoneticName);
  user.nickName && formData.append("nickName", user.nickName);

  return new Promise((resolve, reject) => {
    axios
      .patch(endpoints.userOnboard, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          tempPwd: user.tempPwd,
          coId: user.coId,
          email: user.email,
        },
      })
      .then((res) => {
        localStorage.setItem("coId", res.data.company.id);
        localStorage.setItem("coMbrId", res.data.company.member.id);
        localStorage.setItem("coMbrTkn", res.data.company.member.token);
        localStorage.setItem("siloId", res.data.company.member.silo.id);
        currentJwt.next(res.data.company);
        resolve(res);
      })
      .catch((err) => {
        result = {
          header: err.response ? err.response.data.hdr : "",
          message: err.response ? err.response.data.msg : "",
        };
        reject(result);
      });
  });
}

async function photoWriteAccess(companyId, token, memberId) {
  return await axios.patch(endpoints.photoWriteAccess, {
    headers: {
      coId: localStorage.getItem("coId"),
      coMbrTkn: localStorage.getItem("coMbrTkn"),
      coMbrId: localStorage.getItem("coMbrId"),
    },
  });
}

async function forgotPassword(companyId, email) {
  return await axios.get(endpoints.forgotPassword, {
    headers: {
      coId: companyId,
      email: email,
    },
  });
}

async function forgetPassword(companyMemberId) {
  var formData = new FormData();
  formData.append("id", companyMemberId);

  return await axios.patch(endpoints.forgetPassword, formData, {});
}

async function resetPassword(companyId, token, email, password) {
  var formData = new FormData();
  formData.append("pwd", password);

  var result = {};

  await axios
    .patch(endpoints.resetPassword, formData, {
      headers: {
        coId: companyId,
        coMbrTkn: token,
        coMbrEmail: email,
      },
    })
    .then((res) => {
      if (
        res != null &&
        res.data != null &&
        res.data.company != null &&
        res.data.company.member != null
      ) {
        localStorage.setItem("coId", res.data.company.id);
        localStorage.setItem("coMbrId", res.data.company.member.id);
        localStorage.setItem("coMbrTkn", res.data.company.member.token);
        localStorage.setItem("siloId", res.data.company.member.silo.id);
        currentJwt.next(res.data.company);
        result = res.data;
        result.successful = true;
      }
    })
    .catch((err) => {
      result.successful = false;
      result.error = {
        header: err.response.data.hdr,
        message: err.response.data.msg,
      };
    });

  return result;
}

function logout() {
  // remove user from local storage to log user out
  localStorage.clear();
  sessionStorage.clear();
  currentJwt.next(null);
}
