import React, { Component } from "react";
import { colors } from "./Colors";

class AudioVisualiser extends Component {
  constructor(props) {
    super(props);
    this.maxFPS = 60;
    this.frameCount = 0;
    this.canvas = React.createRef();
  }

  componentDidMount() {
    this.draw();
  }

  componentDidUpdate(prevProps, prevState) {
    this.draw();
  }

  draw() {
    const audioProps = this.props.data;
    const audioData = audioProps;
    const canvas = this.canvas.current;
    const samples = 30;
    const blockSize = Math.floor(audioData.length / samples);
    const filteredData = [];
    for (let i = 0; i < samples; i++) {
      let blockStart = blockSize * i;
      let sum = 0;
      for (let j = 0; j < blockSize; j++) {
        sum = sum + Math.abs(audioData[blockStart + j]);
      }
      filteredData.push(sum / blockSize);
    }
    const multiplier = Math.pow(Math.max(...filteredData), -1);
    var normalizedData = filteredData.map((n) => n * multiplier);

    //var normalizedData = filteredData;

    //const canvas = document.querySelector("canvas");

    const dpr = window.devicePixelRatio || 1;
    const padding = 20;
    canvas.width = canvas.offsetWidth * dpr;
    canvas.height = (canvas.offsetHeight + padding * 2) * dpr;
    const ctx = canvas.getContext("2d");
    ctx.scale(dpr, dpr);
    ctx.translate(0, canvas.offsetHeight / 2 + padding); // set Y = 0 to be in the middle of the canvas

    var startGap = canvas.offsetWidth / 10;
    var endGap = startGap;
    const width = (canvas.offsetWidth - startGap * 2) / normalizedData.length;
    ctx.moveTo(0, 0);
    ctx.lineTo(startGap + width, 0);
    ctx.lineWidth = 4;
    ctx.strokeStyle = "rgb(160,230,247)";
    ctx.stroke();
    const startPos = startGap + width;
    var endPos = 0;
    for (let i = 0; i < normalizedData.length; i++) {
      const x = startPos + width * i;
      let height = normalizedData[i] * canvas.offsetHeight - padding;
      if (height < 0) {
        height = 0;
      } else if (height > canvas.offsetHeight / 2) {
        height = height > canvas.offsetHeight / 2;
      }
      this.drawLineSegment(ctx, colors[i], x, height, width, (i + 1) % 2);
      endPos = x;
    }
    ctx.moveTo(endPos, 0);
    ctx.lineTo(endPos, 0);
    ctx.lineWidth = 4;
    ctx.strokeStyle = "rgb(211,6,177)";
    ctx.lineTo(endPos + endGap, 0);
    ctx.stroke();
  }

  drawLineSegment = (ctx, color, x, height, width, isEven) => {
    ctx.lineWidth = 4;
    ctx.strokeStyle = color;
    ctx.beginPath();
    height = isEven ? height : -height;
    ctx.moveTo(x, 0);
    ctx.lineTo(x + width / 2, height);
    //ctx.arc(x + width / 2, height, width / 2, Math.PI, 0, isEven);
    ctx.lineTo(x + width, 0);
    ctx.stroke();
  };

  render() {
    return <canvas ref={this.canvas} width="1500" height="300" />;
  }
}

export default AudioVisualiser;
