/*eslint-disable*/
import React, { useEffect, useRef, useState } from "react";
import { HubConnectionBuilder, LogLevel } from "@microsoft/signalr";
import { questionService, signalService } from "../../../services";
import TypingQuestion from "../../../components/TypingQuestion/TypingQuestion";
import InteractionQuestionMode from "../../../components/InteractionQuestionMode/InteractionQuestionMode";
import { connect } from "react-redux";
import {
  getQuestionProgress,
  getAnswer,
  getFailedQuestion,
} from "../../../actions/questionActions";
import "./QnA.css";
import styles from "./QnA.module.css";
import micstyles from "../../../components/InteractionQuestionMode/InteractionQuestionMode.module.css";
import AudioAnalyser from "../../../components/Waveform/AudioAnalyser";
import Loading from "../../../components/AskSoffosLoading/Loading";
import TestWaveForm from "../../../components/Waveform/TestWaveForm";
import { NotificationManager } from "../../../components/Shared/Notification";
import mic_off from "../../../images/user-question/mic_off.svg";
import mic_on from "../../../images/user-question/mic_on.png";
import { lang } from "@helpers/langSetting";

const InteractionMode = {
  VOICE: "VOICE",
  TYPING: "TYPING",
};

const QnA = (props) => {
  const [interactionMode, setInteractionMode] = useState(null);
  const [refMode] = useState({ mode: null });
  const [typingAnswer, setTypingAnswer] = useState("");
  const [recordingAnswer, setRecordingAnswer] = useState("");
  const [sliderValue] = useState(50);
  const [phrase, setPhrase] = useState("");
  const [questionId] = useState({ questionId: -1 });
  const [waveData, setWaveData] = useState([]);
  const [ansLoading, setAnsLoading] = useState("useranswer");
  const [micState, setMicState] = useState(false);
  const _timer = useRef();
  const ansRef = useRef();

  const handleToggleMode = (mode) => {
    if (mode === InteractionMode.VOICE) {
      if (mode === interactionMode) {
        setInteractionMode("");
        setRecordingAnswer(" ");
        handleGetAnswer(-1);
      } else {
        setInteractionMode(mode);
      }
      refMode.mode = mode;
      setTypingAnswer("");
    }

    if (mode === InteractionMode.TYPING) {
      if (mode === interactionMode) {
        setTypingAnswer("");
        setInteractionMode("");
      } else {
        setInteractionMode(mode);
      }
      refMode.mode = mode;
      setRecordingAnswer("");
    }
    handleLoading("useranswer");
    setPhrase("");
  };

  const handleGetAnswer = (res) => {
    console.log("questionId ==> ", res);
    questionId.questionId = res;
  };

  const handleLoading = (loading) => {
    setAnsLoading(loading);
    ansRef.current = loading;
  };

  const waker = () => {
    clearTimeout(_timer.current);
    _timer.current = setTimeout(() => {
      sleeper();
    }, 1000 * 60 * 3);
    if (ansRef.current === "soffossleep") {
      handleLoading("useranswer");
    }
  };

  const sleeper = () => {
    handleLoading("soffossleep");
  };

  useEffect(() => {
    document.body.classList.add("user");
    const handleAnswer = async () => {
      await signalService.negotiate().then(async (res) => {
        let data = JSON.parse(res.data);

        const options = {
          accessTokenFactory: () => data.accessToken,
        };

        let connection = new HubConnectionBuilder()
          .withUrl(data.url, options)
          .configureLogging(LogLevel.Information)
          .withAutomaticReconnect()
          .build();

        console.log("websocket connected");
        connection.on("ready", async (quesId) => {
          console.log(questionId.questionId + "-" + quesId);
          let answer = "";
          let phrase = "";
          if (questionId.questionId === quesId) {
            await questionService
              .getAnswer(quesId)
              .then((res) => {
                console.log("this is the answer ==> ", res);
                phrase = res.speech;
                answer = res.text;
              })
              .then((res) => {
                if (refMode.mode === InteractionMode.VOICE) {
                  setRecordingAnswer(answer);
                }

                if (refMode.mode === InteractionMode.TYPING) {
                  console.log("before setting answer", answer, phrase);
                  setTypingAnswer(answer);
                }
                setPhrase(phrase);
                handleLoading("soffosanswer");
              })
              .then(() => {
                props.getAnswer(quesId, answer);
              });
          } else {
            await questionService
              .getAnswer(quesId)
              .then((res) => {
                answer = res.Text;
              })
              .then(() => {
                console.log("call get answer", quesId, answer);
                props.getAnswer(quesId, answer);
              });
          }
        });

        connection.on("fail", function (questionId) {
          console.log("getting answer failed");
          NotificationManager.error(lang("notifications.error.getAnswer"));
          setRecordingAnswer(lang("errorAnswer"));
          props.getFailedQuestion(questionId);
        });

        connection.on("process", function (questionId, percentage) {
          console.log("in processing question", questionId, percentage);
          props.getQuestionProgress(questionId, percentage);
        });

        connection.start().then(() => {
          console.log("Connected");
        });
      });
    };
    //block in temporary
    // handleAnswer();
    waker();
    window.addEventListener("click", waker);
    window.addEventListener("keydown", waker);
    window.addEventListener("mousemove", waker);
    return () => {
      document.body.classList.remove("user");
      window.removeEventListener("click", waker);
      window.removeEventListener("keydown", waker);
      window.removeEventListener("mousemove", waker);
      ansRef.current = null;
    };
  }, []);

  useEffect(() => {
    if (recordingAnswer !== "") {
      setRecordingAnswer("");
    }
  }, [recordingAnswer]);

  return (
    <>
      <div className="qna">
        <div className="loading-answer">
          <Loading loading={ansLoading} />
        </div>
        {!interactionMode && (
          <div className="initial-message">{lang("qna.comment")}</div>
        )}
        {interactionMode === InteractionMode.VOICE && (
          <>
            <TestWaveForm
              recordingAnswer={recordingAnswer}
              onGetAnswer={handleGetAnswer}
              sliderValue={sliderValue}
              phrase={phrase}
              setWaveData={setWaveData}
              setAnsLoading={handleLoading}
              micState={micState}
            />
          </>
        )}
        {interactionMode === InteractionMode.TYPING && (
          <TypingQuestion
            onGetAnswer={handleGetAnswer}
            setTypingAnswer={setTypingAnswer}
            typingAnswer={typingAnswer}
            sliderValue={sliderValue}
            setAnsLoading={handleLoading}
            phrase={phrase}
            setPhrase={setPhrase}
            micState={micState}
          />
        )}
      </div>
      <div className={styles.footer_wrapper}>
        <div className={styles.footer}>
          <div className={styles.web_question}>
            <InteractionQuestionMode
              mode={interactionMode}
              onToggle={handleToggleMode}
            />
          </div>
          <div className={styles.waveform}>
            <AudioAnalyser data={waveData} />
          </div>
          <div className={styles.private_folders}>
            <div className={styles.mobile_question}>
              <InteractionQuestionMode
                mode={interactionMode}
                onToggle={handleToggleMode}
              />
            </div>
            <div
              className={`${micstyles.btn} ${
                micState ? micstyles.clicked : micstyles.unclicked
              }`}
              onClick={() => setMicState(!micState)}
            >
              <div className={micstyles.innerbtn}>
                <img src={micState ? mic_on : mic_off} alt="mic_off" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default connect(null, {
  getQuestionProgress,
  getAnswer,
  getFailedQuestion,
})(QnA);
