import React from "react";
import PropTypes from "prop-types";

class Notification extends React.Component {
  static propTypes = {
    type: PropTypes.oneOf(["info", "success", "warning", "error"]),
    title: PropTypes.node,
    message: PropTypes.node,
    timeOut: PropTypes.number,
    onClick: PropTypes.func,
    onRequestHide: PropTypes.func,
  };

  static defaultProps = {
    type: "info",
    title: null,
    message: null,
    timeOut: 8000,
    onClick: () => {},
    onRequestHide: () => {},
  };

  constructor() {
    super();

    this.notification = React.createRef();
    this.notificationProgress = React.createRef();
  }

  componentDidMount = () => {
    const { timeOut } = this.props;

    if (timeOut !== 0) {
      this.timer = setTimeout(this.requestHide, timeOut);
      setTimeout(() => {
        this.notification.current.classList.add("notification-enter");
        this.notification.current.classList.add("notification-enter-active");
      }, 50);

      let startValue = 0;
      this.intervalTimer = setInterval(() => {
        startValue = startValue + 25;
        let percentage = (startValue / timeOut) * 100;
        this.notificationProgress.current.style.width = `${percentage}%`;
      }, 25);
    }
  };

  componentWillUnmount = () => {
    if (this.timer) {
      clearTimeout(this.timer);
      clearInterval(this.intervalTimer);
    }
  };

  handleClick = () => {
    const { onClick } = this.props;
    if (onClick) {
      onClick();
    }
    this.requestHide();
  };

  requestHide = () => {
    const { onRequestHide } = this.props;
    if (onRequestHide) {
      this.notification.current.classList.add("notification-exit");
      this.notification.current.classList.add("notification-exit-active");

      setTimeout(() => {
        onRequestHide();
      }, 400);
    }
  };

  render() {
    const { type, message } = this.props;
    let { title } = this.props;
    title = title ? <h4 className="title">{title}</h4> : null;
    return (
      <div
        ref={this.notification}
        className={`notification notification-${type}`}
        onClick={this.handleClick}
      >
        <div className="notification-bar"></div>
        <div className="notification-icon">
          {type === "success" && (
            <svg className="success" height="32px" width="32px">
              <circle className="circle" cx="16px" cy="16px" r="11px" />
              <path
                className="checkmark"
                fill="none"
                d="m -4.5 0 z m 0 18 v 6 h 12"
              />
            </svg>
          )}
          {type === "info" && (
            <svg className="info" height="32px" width="32px">
              <circle className="circle" cx="16px" cy="16px" r="11px" />
              <rect
                x="14.5"
                y="8.5"
                width="3px"
                height="8.5px"
                fill="#1296C4"
              />
              <g transform="translate(9,14)">
                <circle className="" cx="7px" cy="7px" r="2px" fill="#1296C4" />
              </g>
            </svg>
          )}
          {type === "warning" && (
            <svg className="warning" height="32px" width="32px">
              <circle className="circle" cx="16px" cy="16px" r="11px" />
              <rect
                x="14.5"
                y="8.5"
                width="3px"
                height="8.5px"
                fill="#E78C09"
              />
              <g transform="translate(9,14)">
                <circle className="" cx="7px" cy="7px" r="2px" fill="#E78C09" />
              </g>
            </svg>
          )}
          {type === "error" && (
            <svg className="error" height="32px" width="32px">
              <circle className="circle" cx="16px" cy="16px" r="11px" />
              <path
                className="remove-icon"
                fill="red"
                d="M11 11 21 21 M21 11 11 21"
              />
            </svg>
          )}
        </div>
        <div className="notification-message" role="alert">
          {title}
          <div className="message">{message}</div>
        </div>
        <div
          ref={this.notificationProgress}
          className="notification-progress"
        ></div>
      </div>
    );
  }
}

export default Notification;
