export const GET_QUESTION = "GET_QUESTION";
export const GET_FAILED_QUESTION = "GET_FAILED_QUESTION";
export const GET_QUESTION_PROGRESS = "GET_QUESTION_PROGRESS";
export const GET_ANSWER = "GET_ANSWER";
export const FETCH_QUESTION_HISTORY = "FETCH_QUESTION_HISTORY";
export const FETCH_LOAD_MORE_QUESTION_HISTORY =
  "FETCH_LOAD_MORE_QUESTION_HISTORY";
export const RESET_QUESTION_HISTORY = "RESET_QUESTION_HISTORY";
export const RATE_ANSWER = "RATE_ANSWER";
export const SEARCH_QUESTION_HISTORY = "SEARCH_QUESTION_HISTORY";

export const fetchQuestionHistory =
  (questions, pageSize, pageCount, orderBy, orderDir, searchBy, searchVal) =>
  (dispatch) => {
    dispatch({
      type: FETCH_QUESTION_HISTORY,
      payload: {
        questions: questions,
        pageSize: pageSize,
        pageCount: pageCount,
        orderBy: orderBy,
        orderDir: orderDir,
        searchBy: searchBy,
        searchVal: searchVal,
      },
    });
  };

export const fetchLoadMoreQuestionHistory =
  (questions, pageSize, pageCount, pageIndex) => (dispatch) => {
    dispatch({
      type: FETCH_LOAD_MORE_QUESTION_HISTORY,
      payload: {
        questions: questions,
        pageSize: pageSize,
        pageCount: pageCount,
        pageIndex: pageIndex,
      },
    });
  };

export const rateAnswer = (quesId, perc) => (dispatch) => {
  dispatch({
    type: RATE_ANSWER,
    payload: { questionId: quesId, percentage: perc },
  });
};

export const getQuestion = (index, quesId, quesText) => {
  var now = new Date();
  var isoDate = new Date(now).toISOString();

  return {
    type: GET_QUESTION,
    payload: {
      index: index,
      questionId: quesId,
      questionText: quesText,
      askedAt: isoDate,
      statusId: "processing",
      progress: 0,
    },
  };
};

export const resetQuestionHistory = () => (dispatch) => {
  dispatch({
    type: RESET_QUESTION_HISTORY,
  });
};

export function getQuestionProgress(quesId, progressPerc) {
  return {
    type: GET_QUESTION_PROGRESS,
    payload: { questionId: quesId, progress: progressPerc },
  };
}

export function getAnswer(quesId, answer) {
  return {
    type: GET_ANSWER,
    payload: { questionId: quesId, answer: answer },
  };
}

export function getFailedQuestion(quesId) {
  return {
    type: GET_FAILED_QUESTION,
    payload: { questionId: quesId },
  };
}

export const searchQuestionHistory =
  (questions, pageSize, pageCount, pageIndex, searchVal) => (dispatch) => {
    dispatch({
      type: SEARCH_QUESTION_HISTORY,
      payload: {
        questions: questions,
        pageSize: pageSize,
        pageCount: pageCount,
        pageIndex: pageIndex,
        searchVal: searchVal,
      },
    });
  };
