import axios from "axios";
import { endpoints } from "./endpoints.config";

export const questionService = {
  discussionTalk,
  getQuestionList,
  getAnswer,
  rateAnswer,
  endTest,
  getTranscripts,
  getTranscriptDetails,
};

async function discussionTalk(question) {
  var headers = {};
  var formData = new FormData();
  formData.append("text", question);
  return await axios.post(endpoints.discussionTalk, formData, {
    headers: headers,
  });
}

async function getQuestionList(
  orderBy = "time",
  orderDir = "desc",
  pageIndex = 1,
  searchBy = "text",
  searchVal = "",
  source
) {
  var data = [];

  let orderByField, orderDirection;

  switch (orderBy) {
    case "time":
      orderByField = "time";
      break;
    case "text":
      orderByField = "text";
      break;
    case "state":
      orderByField = "state";
      break;
    default:
      orderByField = "time";
      break;
  }

  switch (orderDir) {
    case "asc":
      orderDirection = true;
      break;
    case "desc":
      orderDirection = false;
      break;
    default:
      orderDirection = true;
      break;
  }

  var headers = {};

  if (searchVal === "") {
    headers = {
      orderBy: orderByField,
      orderAsc: Number(orderDirection),
      pgIdx: pageIndex,
    };
  } else {
    headers = {
      orderBy: orderByField,
      orderAsc: Number(orderDirection),
      pgIdx: pageIndex,
      searchBy: searchBy,
      searchVal: searchVal,
    };
  }

  await axios
    .get(endpoints.questionList, {
      headers: headers,
      cancelToken: source.token,
    })
    .then((res) => {
      data = res.data;
    })
    .catch((err) => {
      console.log(err);
    });

  return data;
}

async function getAnswer(questionId) {
  var data = "";

  await axios
    .get(endpoints.getAnswer, {
      headers: {
        quesId: questionId,
      },
    })
    .then((res) => {
      data = res.data;
    })
    .catch((err) => {
      console.log(err);
    });

  return data;
}

async function rateAnswer(quesId, value) {
  var formData = new FormData();
  formData.append("quesId", quesId);
  formData.append("perc", value);
  return await axios.post(endpoints.rateAnswer, formData, {});
}

async function endTest(coId, coMbrId, coMbrTkn) {
  let headers = {
    coId,
    coMbrId,
    coMbrTkn,
  };
  return axios.post(endpoints.endTest, {}, { headers: headers });
}

async function getTranscripts() {
  return axios.get(endpoints.getTranscripts, {}, {});
}

async function getTranscriptDetails(topicId) {
  return axios.get(endpoints.getTranscriptDetails, {
    headers: { topicId: topicId },
  });
}
