import React from "react";
import styles from "./Loading.module.css";

const Loading = (props) => {
  const { loading } = props;
  if (loading === "useranswer") {
    return (
      <div className={styles.useranswer}>
        <div className={styles.innericon}>
          <div className={styles.inner}></div>
          <div className={styles.connector}></div>
          <div className={styles.inner}></div>
        </div>
      </div>
    );
  }
  if (loading === "answerloading") {
    return (
      <div className={styles.lds_dual_ring}>
        <div className={styles.useranswer}>
          <div className={styles.inneranswer}>
            <div className={styles.innericon}>
              <div className={styles.inner}></div>
              <div className={styles.connector}></div>
              <div className={styles.inner}></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  if (loading === "soffosanswer") {
    return (
      <div className={styles.soffosanswer}>
        <div className={styles.useranswer}>
          <div className={styles.innericon}>
            <div className={styles.inner}></div>
            <div className={styles.connector}></div>
            <div className={styles.inner}></div>
          </div>
        </div>
      </div>
    );
  }
  if (loading === "soffossleep") {
    return (
      <div className={styles.soffossleep}>
        <div className={styles.innericon}>
          <div className={styles.inner}></div>
          <div className={styles.connector}></div>
          <div className={styles.inner}></div>
        </div>
      </div>
    );
  }
  return null;
};

export default Loading;
