/*eslint-disable*/
import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import { questionService } from "../../services";
import {
  getQuestion,
  rateAnswer,
  getAnswer,
  getQuestionProgress,
} from "../../actions/questionActions";
import { NotificationManager } from "../Shared/Notification";
import styles from "./TypingQuestion.module.css";
import { lang } from "@helpers/langSetting";
import copy from "../../images/user-question/copy.svg";
import soffos from "../../images/knowledge-upload/logo.png";

const TypingQuestion = (props) => {
  const { setAnsLoading, setTypingAnswer, setPhrase, hisList } = props;
  const [question, setQuestion] = useState("");
  const [questionId] = useState({ questionId: -1 });
  const micRef = useRef(false);
  const phraseArrRef = useRef([]);
  const hisListContainer = useRef();
  const [selectedQuesId, setSelectedQuesId] = useState("");

  //for keeping the toggle after rerendering
  const toggleRef = useRef(true);
  const quesRef = useRef();
  const btnRef = useRef();
  const audioRef = useRef();
  const copyRef = useRef();

  //eslint-disable-next-line
  const handleQuesChange = (event) => {
    setQuestion(event.target.value);
    quesRef.current.style.cssText = "height:fit-content; padding:15px";
    var height = quesRef.current.scrollHeight + 2;
    quesRef.current.style.cssText =
      "height:" + height + "px;" + "background-color:var(--bg-white-200);";
  };

  //prevent line break by pressing enter key
  const handleKeyPress = (event) => {
    let key = event.key;
    if (key === "Enter") {
      event.preventDefault();
      btnRef.current.click();
    }
  };

  //toggle change for answer component rerendering
  const handleToggle = () => {
    toggleRef.current = !toggleRef.current;
  };

  const copyQues = (id, text) => {
    setSelectedQuesId(id);
    copyRef.current.value = text;
    copyRef.current.select();
    copyRef.current.setSelectionRange(0, 99999);
    document.execCommand("copy", text);
    setTimeout(() => {
      setSelectedQuesId("");
    }, 1000);
  };

  //send question to server
  const handleAskingQuestion = async (event) => {
    if (question === "") return;
    setAnsLoading("answerloading");
    setTypingAnswer("");
    console.log("question ==> ", question);
    if (question !== "") {
      await questionService
        .discussionTalk(question)
        .then((res) => {
          if (res.data.quesId) {
            questionId.questionId = res.data.quesId;
            var index = res.data.idx;
            console.log(index, questionId.questionId, question);
            props.getQuestion(index, questionId.questionId, question);
            props.onGetAnswer(questionId.questionId);
            props.getAnswer(questionId.questionId, res.data.messages);
            handleToggle();
            setTypingAnswer(res.data.messages);
            if (micRef.current) {
              phraseArrRef.current.push(res.data.speech);
              if (phraseArrRef.current.length === 1) {
                startAudioPlay(res.data.speech);
              }
            }
          } else {
            /* for implement the other cases temporary*/
            let index = Math.floor(Math.random() * 10001);
            let questionId = Math.floor(Math.random() * 10001);
            props.getQuestion(index, questionId, question);
            props.onGetAnswer(questionId);
            props.getQuestionProgress(questionId, 100);
            props.getAnswer(questionId, res.data.messages);
            /* for the new version */
            handleToggle();
            setAnsLoading("soffosanswer");
            setTypingAnswer(res.data.messages);
            if (micRef.current) {
              const audio = new Audio(
                `data:audio/wav;base64,${res.data.speech}`
              );
              audio.play();
            }
          }
        })
        .catch((error) => {
          setAnsLoading("soffosanswer");
          if (error && error.response) {
            NotificationManager.error(
              error.response.data.msg
                ? error.response.data.msg
                : lang("notifications.error.server"),
              error.response.data.hdr
            );
          } else {
            NotificationManager.error(lang("notifications.error.network"));
          }
        });
    }
    setQuestion("");
    quesRef.current.style.cssText = "height:fit-content; padding:15px";
    var height = quesRef.current.scrollHeight + 2;
    quesRef.current.style.cssText =
      "height:" + height + "px;" + "background-color:var(--bg-white-200);";
    setPhrase("");
  };

  const endAudioPlay = () => {
    phraseArrRef.current.shift();
    if (phraseArrRef.current.length > 0) {
      setTimeout(() => {
        startAudioPlay(phraseArrRef.current[0]);
      }, 1000);
    }
  };

  const startAudioPlay = (data) => {
    audioRef.current.src = `data:audio/wav;base64,${data}`;
    audioRef.current.play();
  };

  useEffect(() => {
    if (props.phrase && micRef.current) {
      phraseArrRef.current.push(props.phrase);
      if (phraseArrRef.current.length === 1) {
        startAudioPlay(props.phrase);
      }
    }
  }, [props.phrase]);

  useEffect(() => {
    // var scrollHeight = hisListContainer.current.offsetTop;
    hisListContainer.current.scrollTo(0, 100000000);
  }, [hisList]);

  useEffect(() => {
    micRef.current = props.micState;
  }, [props.micState]);

  useEffect(() => {
    if (props.typingAnswer !== "") {
      handleToggle();
    }
  }, [props.typingAnswer]);

  useEffect(() => {
    quesRef.current.focus();
    const companyId = localStorage.getItem("coId");
    const companyMemberId = localStorage.getItem("coMbrId");
    const companyMemberToken = localStorage.getItem("coMbrTkn");
    return () => {
      questionService
        .endTest(companyId, companyMemberId, companyMemberToken)
        .then((res) => {
          console.log("turn off the stm part", res);
        })
        .catch((error) => {
          NotificationManager.error("end test inform failed!");
        });
      micRef.current = false;
      toggleRef.current = true;
      quesRef.current = undefined;
      btnRef.current = undefined;
    };
  }, []);

  let progress = hisList[hisList.length - 1]?.ques?.Progress + "%";
  console.log(hisList);
  return (
    <>
      <audio preload="auto" ref={audioRef} onEnded={endAudioPlay}></audio>
      <input ref={copyRef} className={styles.copier} />
      <div
        className={`${styles.history} common-scroller`}
        ref={hisListContainer}
      >
        <div className={styles.historyContent}>
          {hisList.map((item) => {
            return (
              <React.Fragment key={item.ques.Id}>
                {item.ques.Text && (
                  <div className={styles.hisQues}>
                    <div>
                      <span className={styles.quesMark}>
                        {/* {lang("label.answer")} */}
                        {props.firstName.toUpperCase().slice(0, 1) +
                          props.lastName.toUpperCase().slice(0, 1)}
                      </span>
                      {item.ques.Text}
                      <img
                        src={copy}
                        alt="copy"
                        onClick={() => copyQues(item.ques.Id, item.ques.Text)}
                      />
                      {selectedQuesId === item.ques.Id && (
                        <span className={styles.copyAlert}>
                          {lang("span.copyAlert")}
                        </span>
                      )}
                    </div>
                  </div>
                )}
                {item.ans.Text && (
                  <div className={styles.hisAns}>
                    <div>
                      <span className={styles.ansMark}>
                        {/* {lang("label.question")} */}
                        <img src={soffos} alt="An" />
                      </span>
                      {item.ans.Text}
                    </div>
                  </div>
                )}
              </React.Fragment>
            );
          })}
        </div>
      </div>
      <div className={styles.wrapper}>
        <div className={styles.textarea_wrapper}>
          <div className={styles.progressWrapper}>
            <div className={styles.progress} style={{ width: progress }}></div>
          </div>
          <textarea
            ref={quesRef}
            className={`${styles.quesArea} common-scroller`}
            rows="1"
            value={question}
            name="question"
            placeholder={lang("placeholder.typeques")}
            spellCheck="false"
            onChange={handleQuesChange}
            onKeyPress={handleKeyPress}
          ></textarea>
        </div>

        <div className={styles.btn_wrapper}>
          <button
            className={styles.btn}
            onClick={handleAskingQuestion}
            ref={btnRef}
          >
            SEND
          </button>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = (state) => {
  return { hisList: state.questions.list };
};
export default connect(mapStateToProps, {
  getQuestion,
  rateAnswer,
  getAnswer,
  getQuestionProgress,
})(TypingQuestion);
